<!---->
<template>
    <!---->
    <el-container>
        <!---->
        <el-header>
            <!---->
            <div>
                <el-header class="header">
                    
                    <hr width="300px" color="#20a0ff">
                </el-header>
            </div>    
        </el-header>

        <!---->
        <el-main>
            <div>
                <!---->
                <el-form :model="schedule"> 
                    <!--<el-row><el-col>-->
                    <!---->
                    <el-row>
                        <el-col :span="24">
                            <el-form-item prop="title">
                                <span style="position:absolute;left: 125px;"></span>
                                <el-input placeholder="" style="width: 73%" v-model="schedule.title"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!---->
                    <el-row>
                        <el-col :span="12">
                            <el-form-item prop="address">
                                <span style="position:absolute;left: 125px;"></span>
                                <el-input placeholder="" style="width: 46%" v-model="schedule.address"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item prop="meetingId">
                                <span></span>
                                <el-select style="width: 50%" v-model="schedule.meetingId" clearable>
                                    <el-option v-for="item in meetings" :key="item.id" :label="item.name" :value="item.name">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!---->
                    <el-row>
                        <el-col :span="12">
                            <el-form-item prop="stratTime">
                                <span style="position:absolute;left: 125px;"></span>
                                <el-input type="date" placeholder="" style="width: 37%" v-model="schedule.startTime"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item prop="endTime">
                                <span></span>
                                <el-input type="date" placeholder="" style="width: 37%" v-model="schedule.endTime"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!---->
                    <el-row>
                        <el-col>
                            <el-form-item prop="schContent">
                                <span style="position:absolute;left: 125px;"></span><br>
                                <el-input placeholder="" type="textarea" :rows="10" style="width: 80%" v-model="schedule.schContent"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!---->
                    <el-row>
                        <el-col>
                            <el-form-item>
                                <span style="position:absolute;left: 125px;"></span>
                                <el-button type="primary" icon="el-icon-edit" size="mini" circle style="position:absolute;left: 200px;" @click="reserve()">></el-button>
                                <br>
                                <el-button type="danger" size="mini" style="position:absolute;left: 125px;"></el-button>
                                <br>
                                <el-input placeholder="" type="textarea" :rows="5" style="position:absolute;left: 125px;width: 20%"></el-input>
                                <br>
                                <br>
                                <br>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!---->
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-checkbox v-model="schedule.ifPrivate"></el-checkbox>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item>
                                <span>{{schedule.createUser}}</span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item>
                                <span>{{schedule.createTime}}</span>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!---->
                    <el-row>
                        <el-col :span="8">
                            <el-button type="primary" @click="addSchedule()"></el-button>
                        </el-col>
                        <el-col :span="8">
                           <el-button type="danger" @click="deleteSchedule()"></el-button>
                        </el-col>
                        <el-col :span="8">
                           <el-button type="info" @click="cancel()"></el-button>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
        </el-main>
    </el-container>
</template>

<!---->
<script>
export default {
    
}
</script>

<!---->
<style scoped>
    #header{
        font-size:20px;
    }
</style>


