<!---->
<template>
    
    <div>
        <!---->
        <div>
            <el-header id="header">
                
                <hr width="100%" style="color:rgb(198,198,198)">
            </el-header>
        </div>

        <!---->
        <el-form :model="branch" ref="addAndUpdateBranch" :rules="rules">
           <div style="margin-top:20px">
             <el-row>
              <el-col :span="8">
                <div style="margin-left: 100px">
                  <el-form-item label=":" prop="branchName">
                    <el-input
                      style="width: 240px;margin: 0px;padding: 0px;"
                      v-model="branch.branchName"
                      size="mini"
                      >
                      </el-input>
                  </el-form-item>
                </div>
              </el-col>
              <el-col :span="8">
                <div style="margin-left: 100px">
                  <el-form-item label=":" prop="branchShortName">
                    <el-input
                      style="width: 240px;margin: 0px;padding: 0px;"
                      v-model="branch.branchShortName"
                      size="mini"
                      >
                      </el-input>
                  </el-form-item>
                </div>
              </el-col>
              <el-col :span="8">
                <div>
                  <el-form-item>
                   <el-button  
                    type="primary" 
                    size="mini" 
                    style="margin-left: 60px;"
                    @click="addMec('addAndUpdateBranch')"
                    :disabled="isDisabled=='1'">
                    
                   </el-button>
                   <el-button  
                    type="primary" 
                    size="mini" 
                    style="margin-left: 10px;"
                    @click="updateMec('addAndUpdateBranch')"
                    :disabled="isDisabled=='0'">
                     
                   </el-button>
                  </el-form-item>
                </div>
              </el-col>
             </el-row>
           </div>
        </el-form>
        <!---->
        <div style="padding-left: 40px;padding-right: 40px;padding-top: 20px;">
            <!--:data="tables"tablestables-->
            <el-table
                :data="branchs"
                border
                stripe
                size="mini"
                style="width: 100%"
                >

                <el-table-column
                    prop="branchName"
                    label=""
                    width=""
                    align="center">
                </el-table-column>

                <el-table-column
                    prop="branchShortName"
                    label=""
                    width="240"
                    align="center">
                </el-table-column>

                <el-table-column
                    width="120"
                    label=""
                    align="center">
                    <template slot-scope="scope">
                        <!---->
                        <el-button @click="showBranchView(scope.row)" style="padding: 3px 4px 3px 4px;margin: 2px"
                           size="mini">
                           <i class="el-icon-edit" ></i>
                </el-button>
                    </template>
                </el-table-column>

                <el-table-column
                    width="120"
                    label=""
                    align="center">
                    <template slot-scope="scope">
                        <!---->
                        <el-button type="danger" style="padding: 3px 4px 3px 4px;margin: 2px" size="mini"
                           @click="deleteMec(scope.row)">
                           <i class="el-icon-delete"></i>
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div style="display: flex;justify-content: space-between;margin: 2px">
              <div></div>
            <el-pagination
              background
              :page-size="10"
              :current-page="currentPage"
              @current-change="currentChange"
              layout="prev, pager, next"
              :total="totalCount">
            </el-pagination>
          </div> 
        </div>
    </div>
    
</template>

<!--Javascript-->
<script>
export default {
    //
    data() {
        return{ 
            isDisabled:0,
            currentPage:1,
            totalCount:-1,
            keywords: '',//

            branch:{//
                branchId: '',
                branchName: '',
                branchShortName: ''
            },
            branchs:[],
            rules: {
              branchName: [{ required: true, message: "", trigger: "blur" }],
              branchShortName: [{ required: true, message: "", trigger: "blur" }]
      }
        }
    },

    //
    mounted: function () {
      this.loadMec();
    },


    //
    methods:{
      loadMec(){
        var _this = this;
        var datas = {
        };
        this.tableLoading = true;
          
        this.postRequest("/home/branchmessage/listBranchVo",datas).then(resp=> {
          this.tableLoading = false;
          if (resp.data && resp.status == 200) {
            _this.branchs = resp.data.obj;
            // console.log("id",resp.data.obj);
          }
        })
      },

      //
      addMec(formName){
          var _this=this;
          this.$refs[formName].validate((valid) => {
            if(valid){
              this.postRequest("/home/branchmessage/addBranch",this.branch).then(resp=> {
                if(resp.data && resp.status == 200){
                  _this.branchs = resp.data.obj;
                  this.$message({type: 'success', message: ""});
                 _this.emptyData();
                  this.loadMec();
                }
              })
            }
          });
      },
      //
      updateMec(formName){
          var _this = this;
          _this.isDisabled=0;
          this.$refs[formName].validate((valid) => {
            if(valid){
              this.postRequest("/home/branchmessage/updateBranch",this.branch).then(resp=> {
                if(resp.data && resp.status == 200){
                  _this.branchs = resp.data.obj;
                 _this.emptyData();
                  this.loadMec();
                }
              })
            }
          });
      },
      showBranchView(row){
        console.log(row);
        this.isDisabled = 1;
        this.branch.branchId=row.branchId;
        this.branch.branchName = row.branchName;
        this.branch.branchShortName = row.branchShortName;
      },
      //
      deleteMec(row){
          this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.doDeleteMec(row.branchId);
        }).catch(() => {
        });
      },
      doDeleteMec(ids){
        this.tableLoading = true;
        var _this = this;
        var datas = {
          branchId:ids
        }
        this.postRequest("/home/branchmessage/deleteBranchByBranchId",datas).then(resp=> {
          _this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            this.$message({type: 'success', message: ""});
            _this.loadMec();

          }
        })
      },
      emptyData(){
       this.branch.branchId="";
        this.branch.branchName = "";
        this.branch.branchShortName = "";
      }
    }
}
</script>>

<!--CSS-->
<style scoped>
    #header{
        font-size:30px;
    }
</style>


