<!---->
<template>
    <div>
        <div>
            <el-header style="font-size:30px;">
                
                <hr width="100%" style="color:rgb(198,198,198)">
            </el-header>
        </div>
        <div style="margin-top:20px">
            <el-button  
                    type="primary" 
                    size="mini" 
                    style="margin-left: 680px;"
                    @click="addEmp()"
                    :disabled="isDisabled=='1'">
                    
                   </el-button>
        </div>
        <!---->  
        <div style="padding-left: 40px;padding-right: 40px;padding-top: 20px;margin-left:180px;">
            <!--:data="tables"tablestables-->
            <el-table
                :data="emps"
                border
                stripe
                size="mini"
                style="width: 80%"
                >
                <el-table-column
                    prop="username"
                    label="ID"
                    width="140"
                    align="center">
                </el-table-column>

                <el-table-column
                    prop="chineseName"
                    label=""
                    width="140"
                    align="center">
                </el-table-column>

                <el-table-column
                    prop="roleName"
                    label=""
                    width=""
                    align="center">
                </el-table-column>

                <el-table-column
                    label=""
                    width="120"
                    align="center">
                    <template slot-scope="scope">
                        <!---->
                        <el-button @click="showEmp(scope.row)" style="padding: 3px 4px 3px 4px;margin: 2px"
                           size="mini">
                           
                        </el-button>
                    </template>
                </el-table-column>

                <el-table-column
                    width="80"
                    label=""
                    align="center">
                    <template slot-scope="scope">
                        <!---->
                        <el-button @click="updateEmp(scope.row)" style="padding: 3px 4px 3px 4px;margin: 2px"
                           size="mini">
                           <i class="el-icon-edit" ></i>
                        </el-button>
                    </template>
                </el-table-column>

                <el-table-column
                    width="80"
                    label=""
                    align="center">
                    <template slot-scope="scope">
                        <!---->
                        <el-button type="danger" style="padding: 3px 4px 3px 4px;margin: 2px" size="mini"
                           @click="deleteEmp(scope.row)">
                           <i class="el-icon-delete"></i>
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<!---->
<script>
export default {
    data(){
        return{
            emp:{
                userId:'',
                username:'',
                chineseName:'',
                roleName:''
            },
            emps:[]
        }
    },
    mounted:function(){
       this.loadEmp();
    },
    methods:{
        loadEmp(){
            var _this = this;
            var datas = {};
            this.tableLoading = true;
          
            this.postRequest("/home/usermessage/listUserVo",datas).then(resp=> {
             this.tableLoading = false;
             if (resp.data && resp.status == 200) {
                _this.emps = resp.data.obj;
            console.log("id",resp.data.obj);
             }
           })
        },
        addEmp(){
              this.$router.push(
              {
               path:'/person/employee/EmployeeSave',
              });
        },
        updateEmp(row){
            console.log("router",this.$router);
            this.$router.push(
              {
               name: '',
               //paramsquery
               params:{
                   userId:row.userId,
                   username:row.username,
                   chineseName:row.chineseName,
                   roleName:row.roleName,
               }
              });
        },
        deleteEmp(row){
          this.$confirm(''+row.chineseName+'', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
            }).then(() => {
          this.doDeleteEmp(row.userId);
          }).catch(() => {
          });
        },
        doDeleteEmp(ids){
            this.tableLoading = true;
            var _this = this;
            var datas = {
              userId:ids
            };
            this.postRequest("/home/usermessage/deleteUserById",datas).then(resp=> {
               _this.tableLoading = false;
              if (resp && resp.status == 200) {
                var data = resp.data;
                this.$message({type: 'success', message: ""});
                _this.loadEmp();
              }
        })
        },
        showEmp(row){
            console.log(this.$router);
            this.$router.push(
              {
               name: '',
               //paramsquery
               params:{
                    userId:row.userId,
                   username:row.username,
                   chineseName:row.chineseName,
                   roleName:row.roleName,
               }
              });
        }
    }
}
</script>
<!---->
<style scoped>
</style>


