<!---->
<template>
    <!---->
    <el-container>
        <!---->
        <el-header>
            <!---->
            <div>
                <el-header class="header">
                    
                    <hr width="300px" color="#20a0ff">
                </el-header>
            </div>
        </el-header>

        <!---->
        <el-main>
            <el-container>
                <!---->
                <div>
                    <el-row>
                        <span class="header"></span>
                        <hr>
                    </el-row>
                    <el-row>
                        <el-tree :data="treeData" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
                    </el-row>
                </div>
                
                <!---->
                <el-main>
                    <el-header>
                        <p align="left" style="margin-top:0">
                            <el-button round type="danger"><i class="el-icon-back" @click="back"></i></el-button>
                            <el-button type="info"><i class="el-icon-back" @click="backRoot"></i></el-button>
                            <el-button type="primary"><i class="el-icon-search" @click="dialogSearch = true"></i></el-button>
                            <el-button type="warning"><i class="el-icon-folder" @click="file"></i></el-button>
                            <el-button type="success"><i class="el-icon-circle-plus-outline" @click="dialogAddFolder = true;dialogFolderName = '';emptyFile()"></i></el-button>
                            <el-button type="success"><i class="el-icon-plus" @click="dialogAddFile = true;dialogFileName = '';emptyFile()"></i></el-button>
                        </p>
                    </el-header>
                    <el-main>
                        <el-form>
                            <el-row>
                                <el-col :span="16">
                                    <el-form-item label=":" label-width="50px">
                                        <el-input v-model="address" style="width:100%"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="1">
                                    <el-form-item>
                                        <el-button type="warning"><i class="el-icon-folder"></i></el-button>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item>
                                        <el-button class="el-icon-refresh-right" type="success"></el-button>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                        </el-form>
                        <el-table :data="file" border size="mini" v-loading="tableLoading" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
                            <el-table-column prop="label" v-model="file.label" align="center" label="" width="200px" fixed="fixed">
                            </el-table-column>
                            <el-table-column prop="fileType" v-model="file.fileType" align="center" label="" width="200px">
                            </el-table-column>
                            <el-table-column prop="remark" v-model="file.remark" align="center" label="" width="200px">
                            </el-table-column>
                            <el-table-column prop="fileOwner" v-model="file.fileOwner" align="center" label="" width="200px">
                            </el-table-column>
                            <el-table-column prop="createDate" v-model="file.createDate" align="center" label="" width="200px">
                            </el-table-column>
                            <el-table-column fixed="right" label="" width="240">
                                <template slot-scope="scope">
                                    <el-button @click="showFile(scope.row)" type="primary" size="mini"></el-button>
                                    <el-button @click="editFile(scope.row)" type="success" size="mini" :disabled="ifButton"></el-button>
                                    <el-button @click="deleteFile(scope.row)" type="danger" size="mini"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>

                        <div v-if="showAccessoryFileTable">
                            <br>
                            <span>{{this.fileName}}</span>
                            <hr color="#20a0ff">
                            <el-table :data="accessoryFile" border size="mini" v-loading="tableLoading" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
                                <el-table-column prop="accessoryName" v-model="file.label" align="center" label="" width="200px" fixed="fixed">
                                </el-table-column>
                                <el-table-column prop="accessoryTypeName" v-model="file.fileType" align="center" label="" width="200px">
                                </el-table-column>
                                <el-table-column prop="accessorySize" v-model="file.remark" align="center" label="KB" width="200px">
                                </el-table-column>
                                <el-table-column prop="accessoryPath" v-model="file.fileOwner" align="center" label="" width="200px">
                                </el-table-column>
                                <el-table-column prop="createDate" v-model="file.createDate" align="center" label="" width="200px">
                                </el-table-column>
                                <el-table-column fixed="right" label="" width="240">
                                    <template slot-scope="scope">
                                        <el-button @click="showAccessoryFile(scope.row)" type="primary" size="mini"></el-button>
                                        <el-button @click="downLoadAccessoryFile(scope.row)" type="success" size="mini"></el-button>
                                        <el-button @click="deleteAccessoryFile(scope.row)" type="danger" size="mini"></el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </div>
                        

                        <!---->
                        <el-dialog :visible.sync="dialogSearch">
                            <!---->
                            <el-container>
                                <!---->
                                <el-header>
                                    <!---->
                                    <div>
                                        <el-header class="header">
                                            
                                            <hr width="300px" color="#20a0ff">
                                        </el-header>
                                    </div>
                                </el-header>

                                <!---->
                                <el-main>
                                </el-main>
                            </el-container>
                        </el-dialog>

                        <!---->
                        <el-dialog :visible.sync="dialogAddFolder">
                            <!---->
                            <el-container>
                                <!---->
                                <el-header>
                                    <!---->
                                    <div>
                                        <el-header class="header">
                                            {{dialogFolderName}}
                                            <hr width="300px" color="#20a0ff">
                                        </el-header>
                                    </div>
                                </el-header>

                                <!---->
                                <el-main>
                                    <el-row>
                                        <el-col span="4">
                                            <span><i class="el-icon-folder" ></i>:</span>
                                        </el-col>
                                        <el-col span="20"> 
                                            <el-input v-model="folderData.label" style="width:100%" size="mini" placeholder=""></el-input>
                                        </el-col>
                                    </el-row>

                                    <br>

                                    <el-row>
                                        <el-col span="4">
                                            <span><i class="el-icon-location-outline"></i></span>
                                        </el-col>
                                        <el-col span="20">
                                            <span>{{this.allData.filePath}}</span>
                                        </el-col>
                                    </el-row>

                                    <br>

                                    <el-row>
                                        <el-col span="4">
                                            <span><i class="el-icon-edit"></i></span>
                                        </el-col>
                                        <el-col span="20">
                                            <el-input v-model="folderData.remark" type="textarea" :rows="3" placeholder="" size="mini"></el-input>
                                        </el-col>
                                    </el-row>

                                    <br>

                                    <el-row>
                                        <el-col span="12">
                                            <span>{{getCurrentDate()}}</span>
                                        </el-col>
                                        <el-col span="12">
                                            <span>{{this.$store.state.user.roles[0].roleName}}</span>
                                        </el-col>
                                    </el-row>

                                    <br>

                                    <el-row>
                                        <br>
                                        <hr style="color: #20a0ff">
                                        <br>
                                        <el-col span="12">
                                            <el-button type="primary" size="mini" @click="addFolder">
                                                
                                            </el-button>
                                        </el-col>
                                        <el-col span="12">
                                            <el-button type="danger" size="mini" @click="cancleFolder">
                                                
                                            </el-button>
                                        </el-col>
                                    </el-row>
                                </el-main>
                            </el-container>
                        </el-dialog>

                        <!--()-->
                        <el-dialog :visible.sync="dialogAddFile">
                            <!---->
                            <el-container>
                                <!---->
                                <el-header>
                                    <!---->
                                    <div>
                                        <el-header class="header">
                                            {{dialogFileName}}
                                            <hr width="300px" color="#20a0ff">
                                        </el-header>
                                    </div>
                                </el-header>

                                <!---->
                                <el-main>
                                    <el-row>
                                        <el-col span="4">
                                            <span><i class="el-icon-folder" ></i></span>
                                        </el-col>
                                        <el-col span="20">
                                            <el-input v-model="oneAccessoryFile.label" size="mini" placeholder=""></el-input>
                                        </el-col>
                                    </el-row>
                                    <br>
                                    <el-row>
                                        <el-col span="4">
                                            <span><i class="el-icon-location-outline"></i></span>
                                        </el-col>
                                        <el-col span="20">
                                            <span>{{this.allData.filePath}}</span>
                                        </el-col>
                                    </el-row>
                                    <br>
                                    <el-row>
                                        <el-col span="4">
                                            <span><i class="el-icon-edit" ></i></span>
                                        </el-col>
                                        <el-col span="20">
                                            <el-input v-model="oneAccessoryFile.remark" type="textarea" :rows="3" placeholder="" size="mini"></el-input>
                                        </el-col>
                                    </el-row>
                                    <br>
                                    <hr style="color: #20a0ff">
                                    <el-row>
                                        <el-col span="12">
                                            <span>{{getCurrentDate()}}</span>
                                        </el-col>
                                        <el-col span="12">
                                            <span>{{this.$store.state.user.roles[0].roleName}}</span>
                                        </el-col>
                                    </el-row>
                                    <br>
                                    <el-row>
                                        <el-col span="4">
                                            <span>>></span>
                                        </el-col>
                                        <el-col span="20">
                                            <template>
                                                <el-radio-group v-model="radio">
                                                    <el-radio :label="1"></el-radio>
                                                    <el-radio :label="2"></el-radio>
                                                    <el-radio :label="3"></el-radio>
                                                    <el-radio :label="4"></el-radio>
                                                    <el-radio :label="5"></el-radio>
                                                    <el-radio :label="6"></el-radio>
                                                    <el-radio :label="7"></el-radio>
                                                    <el-radio :label="8"></el-radio>
                                                    <el-radio :label="9"></el-radio>
                                                    <el-radio :label="10"></el-radio>
                                                    <el-radio :label="11"></el-radio>
                                                    <el-radio :label="12"></el-radio>
                                                </el-radio-group>
                                            </template>
                                        </el-col>
                                    </el-row>
                                    <br>
                                    <hr style="color: #20a0ff">
                                    <el-row v-if="showTable">
                                        <el-col>
                                            <el-table  border stripe size="mini" sytle="width: 100%" :data="oneAccessoryFileData" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
                                                <el-table-column
                                                    prop="accessoryName"
                                                    label=""
                                                    align="center"
                                                    width="150">
                                                </el-table-column>
                                                <el-table-column
                                                    prop="accessorySize"
                                                    label="KB"
                                                    align="center"
                                                    width="100">
                                                </el-table-column>
                                                <el-table-column
                                                    prop="accessoryTypeName"
                                                    label=""
                                                    align="center"
                                                    width="100">
                                                </el-table-column>
                                                <el-table-column
                                                    prop="createDate"
                                                    label=""
                                                    align="center"
                                                    width="150">
                                                </el-table-column>
                                                <el-table-column
                                                    label=""
                                                    align="center"
                                                    width="150"
                                                    fixed="right">
                                                    <template slot-scope="scope">
                                                        <el-button @click="deleteAccessoryFile(scope.row)" type="danger" size="mini"></el-button>
                                                        <el-button @click="downLoadAccessoryFile(scope.row)" type="success" size="mini"></el-button>
                                                    </template>
                                                </el-table-column>
                                            </el-table>
                                        </el-col>
                                    </el-row>
                                    <el-row>
                                        <span style="color: green"></span>
                                    </el-row>
                                    <br>
                                    <el-row>
                                        <el-col span="4">
                                            <span><i class="el-icon-folder"></i></span>
                                        </el-col>
                                    </el-row>
                                    <el-row>
                                        <el-col span="6">
                                            <el-upload
                                                class="upload-demo"
                                                ref="upload"
                                                action="/file/filemanagement/addAccessoryToDesk"
                                                :data="upLoadData"
                                                :before-upload="handleBefore"
                                                :on-preview="handlePreview"
                                                :on-remove="handleRemove"
                                                :file-list="fileList"
                                                :auto-upload="false">
                                                <el-button slot="trigger" size="small" type="primary">...</el-button>
                                                <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
                                            </el-upload>
                                        </el-col>
                                    </el-row>
                                    <br>
                                    <hr style="color: #20a0ff">
                                    <el-row>
                                        <el-col span="12">
                                            <el-button size="mini" type="primary" @click="addFile"></el-button>
                                        </el-col>
                                        <el-col span="12">
                                            <el-button size="mini" type="danger" @click="cancleFile"></el-button>
                                        </el-col>
                                    </el-row>
                                </el-main>
                            </el-container>
                        </el-dialog>

                        <!---->
                        <el-dialog :visible.sync="dialogFileInfo">
                            <!---->
                            <el-container>
                                <!---->
                                <el-header>
                                    <!---->
                                    <div>
                                        <el-header class="header">
                                            
                                            <hr width="300px" color="#20a0ff">
                                        </el-header>
                                    </div>
                                </el-header>

                                <!---->
                                <el-main>
                                </el-main>
                            </el-container>
                        </el-dialog>

                        <!---->
                        <el-dialog :visible.sync="dialogEdit">
                            <!---->
                            <el-container>
                                <!---->
                                <el-header>
                                    <!---->
                                    <div>
                                        <el-header class="header">
                                            
                                            <hr width="300px" color="#20a0ff">
                                        </el-header>
                                    </div>
                                </el-header>

                                <!---->
                                <el-main>
                                </el-main>
                            </el-container>
                        </el-dialog>
                    </el-main>
                </el-main>
            </el-container>
        </el-main>
    </el-container>
</template>

<!---->
<script>
export default {
    data() {
      return {
        treeData: [],
        file: [],
        allData: [],

        address: '',
        formLabelWidth: '120px',
        tableLoading: false,
        defaultProps: {
          children: 'children',
          label: 'label'
        },

        dialogSearch: false,
        dialogAddFolder: false,
        dialogAddFile: false,
        dialogFileInfo: false,
        dialogEdit: false,

        radio: '',//radiolabel
        fileList: [],
        showTable: false,
        dialogFileName: '',
        dialogFolderName: '',
        showAccessoryFileTable: false,
        accessoryFile: [],

        fileName: '',

        oneAccessoryFile: {
            //
            label: '',
            filePath: '',
            remark: '',

            //
            accessoryId: '',
            accessoryName: '',
            accessoryPath: '',
            accessorySize: '',
            accessoryType: '',
            accessoryTypeName: '',
            createDate: '',
            fileId: ''
        },

        oneAccessoryFileData: [],

        folderData: {
            createDate: '',
            fileId: '',
            fileOwner: '',
            filePath: '',
            fileType: '',
            label: '',
            parentId: '',
            remark: ''
        },

        showFileRow: [],//tablerow

        treeClickData: [],//treedata

        ifButton: false,

        //
        upLoadData: {
            accessoryPath: ''
        },

        //
        download: {
            filePath: ''
        },

        //12
        fileTypeData: [
            {
                id: 11,
                type: 'text/plain'

            },
            {
                id: 12,
                type: 'application/msword'

            },
            {
                id: 13,
                type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'

            },
            {
                id: 14,
                type: 'application/vnd.openxmlformats-officedocument.presentationml.presentation'

            },
            {
                id: 15,
                type: 'application/pdf'

            },
            {
                id: 16,
                type: 'image/jpeg'

            },
            {
                id: 17,
                type: '1'

            },
            {
                id: 18,
                type: '1'

            },
            {
                id: 19,
                type: '1'

            },
            {
                id: 20,
                type: '1'

            },
            {
                id: 21,
                type: '1'

            },
            {
                id: 22,
                type: '1'

            },
        ]

        
      }
    },
    mounted: function() {
        this.allData.parentId = 1
        this.allData.filePath = 'D:\\\\MyOffice\\\\\\\\'
        this.getTreeMenu();
        //this.initData();
    },
    methods: {
        //"2019-09-19 12:57:41"
        getCurrentDate() {
            var now = new Date();
            var year = now.getFullYear(); //
            var month = now.getMonth();//
            var date = now.getDate();//
            //var day = now.getDay();//
            var hour = now.getHours();//
            var minu = now.getMinutes();//
            var sec = now.getSeconds();//
            month = month + 1;
            if (month < 10) month = "0" + month;
            if (date < 10) date = "0" + date;
            if (hour < 10) hour = "0" + hour;
            if (minu < 10) minu = "0" + minu;
            if (sec < 10) sec = "0" + sec;
            var time = "";
            //"2019-09-19 12:57:41"
            time = year + "-" + month + "-" + date+ " " + hour + ":" + minu + ":" + sec;
            return time;
        },
        //
        getTreeMenu(){
            this.postRequest("/menu/menuTree").then(resp => {
                if (resp && resp.status == 200) {
                    this.treeData = resp.data.obj;
                    console.log("testTreeData",resp)
                }
            })
        },

        //
        handleNodeClick(data) {
            this.treeClickData = data
            console.log("treeMessageData",data)
            this.allData = data
            this.file = data.children
            this.showAccessoryFileTable = false

        },

        //
        loadTreeTable(data){
            this.file = data.children
        },

        //
        initData: function() {
            this.getRequest("/person/depart/getalldepartinfo").then(resp => {
                if (resp && resp.status == 200) {
                    this.tableData = resp.data;
                }
            })
        },

        //
        back(){

        },

        //
        backRoot(){
            this.$router.go('/file/file/FileTree')
        },

        //
        searchFile(){

        },

        //
        showFile(row){
            this.showFileRow = row
            if(row.fileType == 1){//
                this.$message.warning(``);
            }else{//
                //this.dialogFileName = ''
                //this.dialogAddFile = true
                //this.showTable = true

                this.fileName = row.label

                //
                this.oneAccessoryFile.label = row.label
                this.oneAccessoryFile.filePath = row.filePath
                this.oneAccessoryFile.remark = row.remark
                this.oneAccessoryFile.fileId = row.fileId 

                var datas = {
                    "fileId": this.oneAccessoryFile.fileId
                }
                this.postRequest("/file/filemanagement/listAccessory", datas).then(resp => {
                    if (resp && resp.status == 200) {
                            
                        console.log("accessoryFile",resp);
                        this.accessoryFile = resp.data.obj;

                        if(this.accessoryFile.length === 0){//
                            this.$message.warning(``);
                        }else{//
                            this.showAccessoryFileTable = !this.showAccessoryFileTable
                        }
                    }
                })

                
            
            }
            
        },

        //
        deleteFile(row){
            this.$confirm('[' + row.label + ']?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
            }).then(() => {
                var datas = {
                    "fileId": row.fileId,
                    "parentId": row.parentId
                }
                this.postRequest("/file/filemanagement/MoveFileToRecycleBin",datas).then(resp => {
                    if (resp && resp.status == 200) {
                        //
                        this.getTreeMenu()
                        //this.handleNodeClick(this.treeClickData)
                        //this.loadTreeTable(this.treeClickData)
                    }
                })
            })
            
            
            
        },

        //
        showAccessoryFile(row){
            this.dialogFileName = ''
            this.dialogAddFile = true
            this.showTable = true
            this.oneAccessoryFile.accessoryId = row.accessoryId
            this.oneAccessoryFile.accessoryName = row.accessoryName
            this.oneAccessoryFile.accessoryPath = row.accessoryPath
            this.oneAccessoryFile.accessorySize = row.accessorySize
            this.oneAccessoryFile.accessoryType = row.accessoryType
            this.oneAccessoryFile.accessoryTypeName = row.accessoryTypeName
            this.oneAccessoryFile.createDate = row.createDate
            this.oneAccessoryFile.fileId = row.fileId

            this.oneAccessoryFileData.push(this.oneAccessoryFile)
        },

        //
        emptyFile(){
            //
            this.oneAccessoryFile.label = ''
            //this.oneAccessoryFile.filePath = ''
            this.oneAccessoryFile.remark = ''
        },

        //
        emptyAccessoryFile(){
            //
            //this.oneAccessoryFile.label = ''
            //this.oneAccessoryFile.filePath = ''
            //this.oneAccessoryFile.remark = ''

            //
            this.oneAccessoryFile.accessoryId = ''
            this.oneAccessoryFile.accessoryName = ''
            this.oneAccessoryFile.accessoryPath = ''
            this.oneAccessoryFile.accessorySize = ''
            this.oneAccessoryFile.accessoryType = ''
            this.oneAccessoryFile.accessoryTypeName = ''
            this.oneAccessoryFile.createDate = ''
            //id
            this.oneAccessoryFile.fileId = ''

            this.oneAccessoryFileData.pop()

            //
            this.folderData.createDate = ''
            this.folderData.fileId = ''
            this.folderData.fileOwner = ''
            this.folderData.filePath = ''
            this.folderData.fileType = ''
            this.folderData.label = ''
            this.folderData.parentId = ''
            this.folderData.remark = ''

            //
            this.fileList.pop()
        },

        //
        editFile(row){
            if(row.fileType === 1){//
                this.dialogFolderName = ''
                this.dialogAddFolder = true

                this.folderData.label = row.label
                this.folderData.createDate = row.createDate
                this.folderData.fileId = row.fileId
                this.folderData.fileOwner = row.fileOwner
                this.folderData.filePath = row.filePath
                this.folderData.fileType = row.fileType
                this.folderData.parentId = row.parentId
                this.folderData.remark = row.remark

            }else{//
                //
                this.oneAccessoryFile.accessoryId = row.accessoryId
                this.oneAccessoryFile.accessoryName = row.accessoryName
                this.oneAccessoryFile.accessoryPath = row.accessoryPath
                this.oneAccessoryFile.accessorySize = row.accessorySize
                this.oneAccessoryFile.accessoryType = row.accessoryType
                this.oneAccessoryFile.accessoryTypeName = row.accessoryTypeName
                this.oneAccessoryFile.createDate = row.createDate

                //id
                this.oneAccessoryFile.fileId = row.fileId 

                var datas = {
                    "fileId": this.oneAccessoryFile.fileId
                }
                this.postRequest("/file/filemanagement/listAccessory", datas).then(resp => {
                    if (resp && resp.status == 200) {
                                
                        console.log("accessoryFile",resp);
                        this.accessoryFile = resp.data.obj;

                        if(this.accessoryFile.length === 0){//
                            this.dialogFileName = ''
                            this.dialogAddFile = true

                            //
                            this.oneAccessoryFile.label = row.label
                            this.oneAccessoryFile.filePath = row.filePath
                            this.oneAccessoryFile.remark = row.remark

                        }else{//
                            this.$message.warning(``);
                        }
                    }
                })

            }


        },

        //
        deleteAccessoryFile(row){
            this.$confirm('[' + row.accessoryName + ']?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
            }).then(() => {
                var datas = {
                    "accessoryId": row.accessoryId,
                    "accessoryPathAndName": row.accessoryPath,
                }
                this.postRequest("/file/filemanagement/deleteAccessory",datas).then(resp => {
                    if (resp && resp.status == 200) {
                        //
                        this.showFile(this.showFileRow)
                        this.showAccessoryFileTable = true
                    }
                })
            })

            

        },

        //
        addFolder(){
            this.dialogAddFolder = false

            if(this.folderData.fileId === ''){//id
                this.folderData.filePath = this.allData.filePath
                this.folderData.fileId = this.allData.fileId
                
                var datas = {
                    "createDate": this.getCurrentDate(),
                    "fileId": null,
                    "fileOwner": this.$store.state.user.roles[0].roleName,
                    "filePath": this.folderData.filePath,
                    "fileType": 1,
                    "label": this.folderData.label,
                    "parentId": this.folderData.fileId,
                    "remark": this.folderData.remark
                }
                this.postRequest("/file/filemanagement/addFile",datas).then(resp => {
                    if (resp && resp.status == 200) {
                        //
                        this.getTreeMenu() 
                    }
                })
                this.emptyAccessoryFile()

            }else{//id
                var datas = {
                    "fileId": this.folderData.fileId,
                    "label": this.folderData.label,
                    "remark": this.folderData.remark
                }
                this.postRequest("/file/filemanagement/editFile",datas).then(resp => {
                    if (resp && resp.status == 200) {
                        //
                        this.getTreeMenu() 
                    }
                })
                this.emptyAccessoryFile()
            }

        },

        //
        cancleFolder(){
            this.dialogAddFolder = false
            this.emptyAccessoryFile()
        },

        //
        addFile(){
            this.dialogAddFile = false
            this.showTable = false

            if(this.oneAccessoryFile.fileId === ''){
                //this.oneAccessoryFile.filePath = this.allData.filePath
                // //
                // var datas = {
                //     "createDate": this.getCurrentDate(),
                //     "fileId": null,
                //     "fileOwner": this.$store.state.user.roles[0].roleName,
                //     "filePath": this.oneAccessoryFile.filePath,
                //     "fileType": 2,
                //     "label": this.oneAccessoryFile.label,
                //     "parentId": this.allData.fileId,
                //     "remark": this.oneAccessoryFile.remark
                // }
                // this.postRequest("/file/filemanagement/addFile",datas).then(resp => {
                //     if (resp && resp.status == 200) {
                //         //
                //         this.getTreeMenu()     
                //     }
                // })

                //
                var accessoryFileData = {
                    "accessoryName": this.oneAccessoryFile.accessoryName,
                    "accessoryPath": this.oneAccessoryFile.filePath+"\\\\"+this.oneAccessoryFile.label+"\\\\"+this.oneAccessoryFile.accessoryName,
                    "accessorySize": this.oneAccessoryFile.accessorySize,
                    "accessoryType": this.oneAccessoryFile.accessoryType,
                    "createDate": this.getCurrentDate(),
                    "fileId": null
                }
                this.postRequest("/file/filemanagement/addAccessoryToDb",accessoryFileData).then(resp => {
                    if (resp && resp.status == 200) {
                        //
                        this.getTreeMenu() 
                        //
                        //this.showFile(this.showFileRow)
                        //this.showAccessoryFileTable = true
                    }
                })
            }else{
                //
                var accessoryFileData = {
                    "accessoryName": this.oneAccessoryFile.accessoryName,
                    "accessoryPath": this.oneAccessoryFile.filePath+"\\\\"+this.oneAccessoryFile.accessoryName,
                    "accessorySize": this.oneAccessoryFile.accessorySize,
                    "accessoryType": this.oneAccessoryFile.accessoryType,
                    "createDate": this.getCurrentDate(),
                    "fileId": this.oneAccessoryFile.fileId
                }
                this.postRequest("/file/filemanagement/addAccessoryToDbHasFileId",accessoryFileData).then(resp => {
                    if (resp && resp.status == 200) {
                        //
                        this.getTreeMenu()  
                        //
                        //this.showFile(this.showFileRow)
                        //this.showAccessoryFileTable = true   
                    }
                })
            }

            

            this.emptyAccessoryFile()
        },

        //
        cancleFile(){
            this.dialogAddFile = false
            this.showTable = false
            
            if(this.oneAccessoryFile.fileId === ''){//
                //
                var datas = {
                    "accessoryPathAndName": this.upLoadData.accessoryPath,
                    "filePathAndName": this.oneAccessoryFile.filePath
                }
                this.postRequest("/file/filemanagement/exitTwo",datas).then(resp => {
                    if (resp && resp.status == 200) {
                        //
                        this.getTreeMenu()  
                        //
                        // this.showFile(this.showFileRow)
                        // this.showAccessoryFileTable = true   
                    }
                })

                //
                this.emptyAccessoryFile()
                
            }else{//
                //
                var datas = {
                    "accessoryId": this.oneAccessoryFile.accessoryId,
                    "accessoryPathAndName": this.upLoadData.accessoryPath
                }
                this.postRequest("/file/filemanagement/exit",datas).then(resp => {
                    if (resp && resp.status == 200) {
                        //
                        this.getTreeMenu()  
                        //
                        // this.showFile(this.showFileRow)
                        // this.showAccessoryFileTable = true   
                    }
                })

                //
                this.emptyAccessoryFile()
            }

            
        },

        //
        //submit	
        submitUpload() {
            //
            if(this.oneAccessoryFile.fileId === ''){//
                this.oneAccessoryFile.filePath = this.allData.filePath
                //
                var datas = {
                    "createDate": this.getCurrentDate(),
                    "fileId": null,
                    "fileOwner": this.$store.state.user.roles[0].roleName,
                    "filePath": this.oneAccessoryFile.filePath,
                    "fileType": 2, 
                    "label": this.oneAccessoryFile.label,
                    "parentId": this.allData.fileId,
                    "remark": this.oneAccessoryFile.remark
                }
                this.postRequest("/file/filemanagement/addFile",datas).then(resp => {
                    if (resp && resp.status == 200) {
                        //
                        this.oneAccessoryFile.filePath = this.oneAccessoryFile.filePath+'\\\\'+this.oneAccessoryFile.label
                        //

                        this.$refs.upload.submit();
                        this.showTable = true



                        this.oneAccessoryFileData.push(this.oneAccessoryFile)

                        //
                        this.getTreeMenu()     
                    }
                })
                
                
            }else{//
                //

                this.$refs.upload.submit();
                this.showTable = true

                this.oneAccessoryFileData.push(this.oneAccessoryFile)
            }
            
            
        },
        //on-remove	
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        //on-preview	
        handlePreview(file) {
            console.log(file);
        },

        //before-upload 
        //++
        handleBefore(file){
            
            //this.oneAccessoryFile.accessoryType = file.type

            this.oneAccessoryFile.accessoryTypeName = file.type

            for(var i = 0;i<this.fileTypeData.length;i++){
                if(file.type === this.fileTypeData[i].type ){
                    this.oneAccessoryFile.accessoryType = this.fileTypeData[i].id;
                    break;
                }
            }

            this.oneAccessoryFile.accessorySize = file.size/1024
            this.oneAccessoryFile.accessoryName = file.name
            this.upLoadData.accessoryPath = this.oneAccessoryFile.filePath+'\\\\'+file.name
            console.log("",this.oneAccessoryFile.accessoryType)
            //return this.upLoadData.accessoryFilePat
            
        },

        //
        downLoadAccessoryFile(row){

            console.log("accessoryPath",row.accessoryPath)

            // this.download.filePath = row.accessoryPath
            
            // this.getRequest("/file/filemanagement/testDownload",this.download).then(resp => {
            //     if (resp && resp.status == 200) {
            //         this.$message({type: 'success', message: ""});
            //     }
            // })

            //iframe
            var elemIF = document.createElement('iframe')
            elemIF.src = '/file/filemanagement/testDownload?filePath=' + row.accessoryPath 
            elemIF.style.display = 'none'
            document.body.appendChild(elemIF)

        },
    }
}
</script>

<!---->
<style scoped>
    .header{
        font-size:20px;text-align: center;
    }
</style>


