import {getRequest} from './api'
import {Message} from 'element-ui'

//
export const isNotNullORBlank = (...args)=> {
  for (var i = 0; i < args.length; i++) {
    var argument = args[i];
    if (argument == null || argument == '' || argument == undefined) {
      Message.warning({message: '!'})
      return false;
    }
  }
  return true;
}

//
export const initMenu = (router, store)=> {
  if (store.state.routes.length>0) {//store.state.routes
    return;
  }
  
  //store.state.routes
  getRequest("/config/sysmenu").then(resp=> {
    
    if (resp && resp.status == 200) {
      console.log("MenuDatas",resp);
      var arr = [];//
      //arr.push(resp.data[1]);//arr
      arr = resp.data
      var fmtRoutes = formatRoutes(arr);//formatRoutes(),fmtRoutes
      console.log("formatRoutes",fmtRoutes);
      router.addRoutes(fmtRoutes);//
      store.commit('initMenu', fmtRoutes);//commit  this.$store.commit('mutations'arg),(storeinitMenufmtRoutesstoreinitMenumenus)
      store.dispatch('connect');//dispatch  this.$store.dispatch('actions'arg)
    }
  })
}

//
export const formatRoutes = (routes)=> {
  let fmRoutes = [];

  //for
  routes.forEach(router=> {//routesrouter
    let {//router
      path,//routerpath
      component,//vue
      displayName,//
      //meta,
      //iconCls,//?
      children//
    } = router;

    //
    //
    if (children && children instanceof Array) {
      children = formatRoutes(children);
    }

    let fmRouter = {
      path: path,
      //()
      //author: lmh <minhao.liu@capgemini.com>
      component(resolve){
        //ifcomponent
        //ifvue
        if (component.startsWith("Home")) {//
          require(['../components/public/' + component + '.vue'], resolve)
        } 
        else if (component.startsWith("MechanismIndex")) {//
          require(['../components/person/mechanism/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("DepartmentIndex")) {//
          require(['../components/person/department/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("EmployeeIndex")) {//
          require(['../components/person/employee/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("MyselfIndex")) {//
          require(['../components/schedule/myself/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("DepartmentIndex2")) {//
          require(['../components/schedule/department/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("NoteIndex")) {//
          require(['../components/schedule/note/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("FileTree")) {//
          require(['../components/file/file/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("RecycleBinIndex")) {//
          require(['../components/file/recycle_bin/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("FileSearchIndex")) {//
          require(['../components/file/file_search/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("NewsIndex")) {//
          require(['../components/news/news/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("MailboxIndex")) {//
          require(['../components/news/mailbox/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("RoleIndex")) {//
          require(['../components/system/role/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("LoginRecordIndex")) {//
          require(['../components/system/login_record/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("MenuSortIndex")) {//
          require(['../components/system/menu_sort/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("OperationRecordIndex")) {//
          require(['../components/system/operation_record/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("AttendanceCardIndex")) {//
          require(['../components/attendance/attendance_card/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("AttendanceRecordIndex")) {//
          require(['../components/attendance/attendance_record/' + component + '.vue'],resolve)
        }
        else if (component.startsWith("AttendanceStatisticsIndex")) {//
          require(['../components/attendance/attendance_statistics/' + component + '.vue'],resolve)
        }

      },
      displayName: displayName,
      //iconCls: iconCls,
      //meta: meta,
      children: children
    };

    fmRoutes.push(fmRouter);//fmRouterfmRoutes

  })
  
  return fmRoutes;
}




