<!---->
<template>
    <!---->
    <el-container>
        <!---->
        <el-header>
            <!---->
            <div>
                <el-header class="header">
                    
                    <hr width="300px" color="#20a0ff">
                </el-header>
            </div>
        </el-header>

        <!---->
        <el-main>
            <div>
                <!---->
                <el-row>
                    <el-col>
                        <el-button type="primary" icon="el-icon-circle-plus" @click="dialogNoteVisible = true"></el-button>
                    </el-col>
                </el-row>
                <br>
                <!---->
                <!---->
                <el-row>
                    <el-col>
                        <el-table border stripe size="mini" sytle="width: 100%" :data="noteList" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
                            <!---->
                            <el-table-column
                                prop="noteId"
                                label=""
                                align="center"
                                width="100">
                            </el-table-column>

                            <!---->
                            <el-table-column
                                prop="noteTitle"
                                label=""
                                align="center"
                                width="800">
                            </el-table-column>

                            <!---->
                            <el-table-column
                                prop="operation"
                                label=""
                                align="center"
                                width="354">
                                    <template slot-scope="scope">
                                        <!---->
                                        <el-button type="primary" style="padding: 3px 4px 3px 4px;margin: 2px" size="mini" @click="lookNote(scope.row.noteId)">
                                            
                                        </el-button>
                                    </template>
                            </el-table-column>
                        </el-table>
                    </el-col>
                </el-row>

                <!---->
                <el-row>
                </el-row>
            </div>

            <!---->
            <el-dialog :title="dialogNoteTitle" :visible.sync="dialogNoteVisible">
                <!---->
                <el-form :model="note">
                    <!--<el-row><el-col>-->
                    <el-row>
                        <el-col>
                            <el-form-item>
                                <span style="position:absolute;left:10px;"></span><br>
                                <el-input placeholder="" v-model="note.noteTitle">

                                </el-input>
                            </el-form-item>
                        </el-col>

                        <el-col>
                            <el-form-item>
                                <span style="position:absolute;left:10px;"></span><br>
                                <el-input placeholder="" type="textarea" :rows="10" v-model="note.noteContent">

                                </el-input>
                            </el-form-item>
                        </el-col>

                        <el-col>
                            <el-form-item>
                                <span>{{this.$store.state.user.username}}</span>
                                <span>{{getCurrentDate()}}</span>
                            </el-form-item>
                        </el-col>

                        <el-col>
                            <el-button type="primary" @click="addNote"></el-button>
                        </el-col>
                    </el-row>
                </el-form>
            </el-dialog>

            <!---->
            <el-dialog :title="dialogNoteDetailTitle" :visible.sync="dialogNoteDetailVisible">
                <!---->
                <el-form :model="noteDetail">
                    <!--<el-row><el-col>-->
                    <el-row>
                        <el-col>
                            <el-form-item>
                                <span style="position:absolute;left:10px;"></span><br>
                                <el-input placeholder="" v-model="noteDetail.noteTitle">

                                </el-input>
                            </el-form-item>
                        </el-col>

                        <el-col>
                            <el-form-item>
                                <span style="position:absolute;left:10px;"></span><br>
                                <el-input placeholder="" type="textarea" :rows="10" v-model="noteDetail.noteContent">

                                </el-input>
                            </el-form-item>
                        </el-col>

                        <el-col>
                            <el-form-item>
                                <span>{{this.$store.state.user.username}}</span>
                                <span>{{noteDetail.createTime}}</span>
                            </el-form-item>
                        </el-col>

                        <el-col>
                            <el-button type="primary" @click="updateNote"></el-button>
                            <el-button type="danger" @click="deleteNote"></el-button>
                        </el-col>
                    </el-row>
                </el-form>
            </el-dialog>

        </el-main>
    </el-container>
</template>

<!---->
<script>
export default {
    //
    data(){
        return{
            noteList: [],
            notesList: [],
            note: {
                noteId: '',
                noteTitle: '',
                noteContent: '',
                createTime: '',
                createUser: ''
            },
            noteDetail: {
                noteId: '',
                noteTitle: '',
                noteContent: '',
                createTime: '',
                createUser: ''
            },
            formLabelWidth: '120px',
            dialogTableVisible: false,
            dialogNoteVisible: false,
            dialogNoteTitle: '',
            dialogNoteDetailVisible: false,
            dialogNoteDetailTitle: '',
        }
    },

    //
    methods:{
        //"2019-09-19 12:57:41"
        getCurrentDate() {
            var now = new Date();
            var year = now.getFullYear(); //
            var month = now.getMonth();//
            var date = now.getDate();//
            //var day = now.getDay();//
            var hour = now.getHours();//
            var minu = now.getMinutes();//
            var sec = now.getSeconds();//
            month = month + 1;
            if (month < 10) month = "0" + month;
            if (date < 10) date = "0" + date;
            if (hour < 10) hour = "0" + hour;
            if (minu < 10) minu = "0" + minu;
            if (sec < 10) sec = "0" + sec;
            var time = "";
            //"2019-09-19 12:57:41"
            time = year + "-" + month + "-" + date+ " " + hour + ":" + minu + ":" + sec;
            return time;
        },

        //
        loadMyNote() {
            var _this = this
            // var datas =  {
            // //'createUser': this.$store.state.user.cname
            // //'createUser': "string"
            //     'userName': 1
            // }
            var userName = this.$store.state.user.username
            this.getRequest('/mynote/listnotebyName/'+userName).then(resp => {
            if (resp.data && resp.data.status === 200) {
                _this.noteList = resp.data.obj;
            }
            })
        },
        //
        addNote(){
            var _this = this
            this.dialogShowNoteVisible = false
            //this.note.createUser=this.$store.state.user.cname
            this.note.createUser = this.$store.state.user.username
            this.note.createTime="2019-09-19 12:57:41"
            if(this.note.noteTitle!=null && this.note.noteTitle!=""){
                this.postRequest('/mynote/addnote', this.note).then(resp => {
                    console.log("",resp);
                    if (resp && resp.status === 200) {
                        this.dialogNoteVisible = false
                        _this.loadMyNote()
                        this.$message({
                        type: 'success',
                        message: ""
                        });
                    }
                })
            }         
            else{
                this.$message({
                    type: 'error',
                    message: ""
                });
            }
        },
        //
        deleteNote() {
            this.$confirm('[' + this.noteDetail.noteTitle + ']?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
            }).then(() => {
                var datas =  {
            noteId: this.noteDetail.noteId
            } 
        
            this.postRequest('/mynote/deletenote', {noteId: this.noteDetail.noteId}).then(resp => {
                if (resp && resp.status === 200) {
                this.dialogNoteDetailVisible = false
                this.loadMyNote()
                }
            })
            }).catch(() => {})
        },
        //
        updateNote() {
            var datas = {
                'noteId': this.noteDetail.noteId,
                'noteTitle': this.noteDetail.noteTitle,
                'noteContent': this.noteDetail.noteContent
            }
            this.postRequest('/mynote/updatenote', datas).then(resp => {
                if (resp && resp.status === 200) {
                this.dialogNoteDetailVisible = false
                this.loadMyNote()
                }
            })
        },
        //
        lookNote(noteId) {
            
            this.dialogNoteDetailVisible = true
            this.initMyNote(noteId)
            // console.log(this.notesList)
            // this.note.noteId = this.notesList.noteId
            // this.note.noteTitle = this.notesList.noteTitle
            // this.note.noteContent = this.notesList.noteContent
            // this.note.createUser = this.notesList.createUser
            // this.note.createTime = this.notesList.createTime
        },
        //
        initMyNote(noteId) {
            var _this = this
            // var datas =  {
            // 'userId': title
            // }
            this.getRequest('/mynote/notebynoteid/'+noteId).then(resp => {
            if (resp && resp.status === 200) {
                _this.notesList = resp.data.obj
                console.log(this.notesList[0])
                this.noteDetail.noteId = this.notesList[0].noteId
                this.noteDetail.noteTitle = this.notesList[0].noteTitle
                this.noteDetail.noteContent = this.notesList[0].noteContent
                this.noteDetail.createUser = this.notesList[0].createUser
                this.noteDetail.createTime = this.notesList[0].createTime
            }
            })
        },
    },

    //
    mounted() {
      this.loadMyNote()
      // this.initMyNote()
    },
    
}
</script>

<!---->
<style scoped>
    .header{
        font-size:20px;text-align: center;
    }
</style>


