<!---->
<template>
    <!---->
    <el-container>
        <!---->
        <el-header>
            <!---->
            <div>
                <el-header class="header">
                    
                    <hr width="300px" color="#20a0ff">
                </el-header>
            </div>
        </el-header>

        <!---->
        <el-main>
            <!---->
            <el-form style="width:90%;margin:0 auto">
                <!---->
                <fieldset>
                    <legend align="left"></legend>
                    <!---->
                    <div style="width:39%;float:left;">
                        <!---->
                        <img src="../../../assets/logo.png" alt="" style="width: 250px;height: 250px;">
                    </div>
                    <el-table :data="mailBox" border style="width:60%;float:right" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
                        <el-table-column prop="file" label="" align="center" >
                            <template slot-scope="scope" style="color: red">
                                <i class="el-icon-message" @click="showDetail(scope.row)" ></i>{{scope.row.file}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="fileNum" label="" align="center"></el-table-column>
                        <el-table-column prop="noReadFile" label="" align="center"></el-table-column>
                    </el-table>
                </fieldset>
            </el-form>


            <!---->
            <el-dialog title=""  :visible.sync="dialogInboxVisible">
                <!---->
                <el-table :data="messages" border @selection-change="handleSelectionChange" :header-cell-style="{background:'#eef1f6',color:'#606266'}" size="mini" >
                    <el-table-column type="selection" fixed="left"></el-table-column>
                    <!-- 
                    <el-table-column prop="messageId" label="Id" width="150px" align="center">
                    </el-table-column> -->

                    <el-table-column prop="createUserName" label="" width="150px" align="center">
                    </el-table-column>

                    <el-table-column prop="messageTitle" label="" width="250px" align="center">
                    </el-table-column>

                    <el-table-column prop="beginTime" label="" width="200px" align="center">
                    </el-table-column>

                    <el-table-column prop="messageTypeName" label="" align="center">
                    </el-table-column>

                    <el-table-column label="" align="center" fixed="right" width="200px">
                        <template slot-scope="scope">
                            <el-button @click="showMessageDetail(scope.row)" :type="ifColor(scope.row)">{{ifRead(scope.row)}}</el-button>
                              <!-- <el-button @click="deleteInboxMessage(scope.row)"></el-button> -->
                        </template>
                    </el-table-column>
                </el-table>

                <div slot="footer" class="dialog-footer" align="right">
                    <el-button type="danger" @click="deleteInboxMessage()" size="mini"></el-button>
                    <el-button @click="dialogInboxVisible = false" size="mini"> </el-button>
                </div>
                

                <!--(2)-->
                <el-dialog title="" :visible.sync="dialogMessageDetail" append-to-body>
                    <el-form :model="message1">

                         <el-row>
                            <el-col :span="12">
                                <el-form-item label="Id">
                                    <span>{{this.message1.messageId}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{this.message1.messageTitle}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{this.message1.messageTypeName}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{this.message1.messageContent}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{this.sender}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{this.message1.publishTime}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                    <div slot="footer" class="dialog-footer" align="center">
                        <el-button @click="dialogMessageDetail = false" size="mini"> </el-button>
                    </div>
                </el-dialog>
            </el-dialog>



            <!---->
            <el-dialog title="" :visible.sync="dialogSendedVisible">
                <!---->
                <el-table :data="sendDetail" border @selection-change="handleSelectionChange" :header-cell-style="{background:'#eef1f6',color:'#606266'}" size="mini">
                    <el-table-column type="selection" fixed="left"></el-table-column>


                    <el-table-column prop="messageTitle" label="" width="250px" align="center">
                        
                    </el-table-column>
                    <el-table-column prop="beginTime" label="" width="200px" align="center">
                       
                    </el-table-column>
                    <el-table-column prop="messageTypeName" label="" align="center">
                    </el-table-column>
<!-- 
                    <el-table-column prop="recipients[].chineseName" label="" align="center">
                    </el-table-column> -->


                    
                    <el-table-column label="" align="center" fixed="right" width="200px">
                        <template slot-scope="scope">
                            <el-button @click="showMessageDetail(scope.row)"></el-button>
                            <!-- <el-button @click="deleteSendedMessage(scope.row)"></el-button> -->
                        </template>
                    </el-table-column>

                     </el-table>

                <div slot="footer" class="dialog-footer" align="right">
                    <el-button type="danger" @click="deleteSendedMessage()" size="mini"></el-button>
                    <el-button @click="dialogSendedVisible = false" size="mini"> </el-button>
                </div>

                <!---->
                <el-dialog title="" :visible.sync="dialogSendedMessageDetail" append-to-body>
                    <el-form :model="messageDetail">
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="ID">
                                    <span>{{messageDetail.title}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.massageDesc}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.content}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.fromUser}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.recordTime }}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                    <div slot="footer" class="dialog-footer" align="center">
                        <el-button @click="dialogSendedMessageDetail = false" size="mini"> </el-button>
                    </div>
                </el-dialog>
            </el-dialog>


            <!---->
            <el-dialog title="" :visible.sync="dialogDraftVisible">
                <!---->
                 <el-table :data="draftDetail" border @selection-change="handleSelectionChange" :header-cell-style="{background:'#eef1f6',color:'#606266'}" size="mini">
                    <el-table-column type="selection" fixed="left"></el-table-column>
                     
                    <!-- <el-table-column  prop="messageId" label="Id" width="150px" align="center"></el-table-column> -->


                    <el-table-column prop="messageTitle" label="" width="250px" align="center">
                        
                    </el-table-column>
                    <el-table-column prop="beginTime" label="" width="200px" align="center">
                       
                    </el-table-column>
                    <el-table-column prop="messageTypeName" label="" align="center">
                    </el-table-column>

                    
                    <el-table-column label="" align="center" fixed="right" width="200px">
                        <template slot-scope="scope">
                            <el-button @click="showMessageDetail(scope.row)"></el-button>
                            <!-- <el-button @click="deleteDraftMessage(scope.row)"></el-button> -->
                        </template>
                    </el-table-column>
                </el-table>

                <div slot="footer" class="dialog-footer" align="right">
                    <el-button type="danger" @click="deleteDraftMessage()" size="mini"></el-button>
                    <el-button @click="dialogDraftVisible = false" size="mini"> </el-button>
                </div>

                <!---->
                <el-dialog title="" :visible.sync="dialogDraftMessageDetail" append-to-body>
                    <el-form :model="messageDetail">
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.title}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.massageDesc}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.content}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        
                        
                    </el-form>
                    <div slot="footer" class="dialog-footer" align="center">
                        <el-button @click="dialogDraftMessageDetail = false" size="mini"> </el-button>
                    </div>
                </el-dialog>
            </el-dialog>



            <!---->
            <el-dialog title="" :visible.sync="dialogDeletedVisible">
                <!---->
                 <el-table :data="deleteDetail" border @selection-change="handleSelectionChange" :header-cell-style="{background:'#eef1f6',color:'#606266'}" size="mini">
                    <el-table-column type="selection" fixed="left"></el-table-column>
                     
                    <!-- <el-table-column  prop="messageId" label="Id" width="150px" align="center"> -->
                        
                
                    <el-table-column prop="messageTitle" label="" width="250px" align="center">
                        
                    </el-table-column>
                    <el-table-column prop="beginTime" label="" width="200px" align="center">
                       
                    </el-table-column>
                    <el-table-column prop="messageTypeName" label="" align="center">
                    </el-table-column>

                    
                    <el-table-column label="" align="center" fixed="right" width="200px">
                        <template slot-scope="scope">
                            <el-button @click="showMessageDetail(scope.row)" :type="ifColor(scope.row)">{{ifRead(scope.row)}}</el-button>
                             <!-- <el-button @click="deleteDeletedMessage(scope.row)"></el-button> -->
                        </template>
                    </el-table-column>
                </el-table>

                <div slot="footer" class="dialog-footer" align="right">
                    
                    <el-button type="danger" @click="deleteMultipMessage" size="mini"></el-button>
                    <el-button @click="dialogDeletedVisible = false" size="mini"> </el-button>
                    
                </div>

    
                <!---->
                <el-dialog title="" :visible.sync="dialogDeletedMessageDetail" append-to-body>
                    <el-form :model="messageDetail">
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.title}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.massageDesc}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.content}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.fromUser}}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <span>{{messageDetail.recordTime }}</span>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                    <div slot="footer" class="dialog-footer" align="center">
                        <el-button @click="dialogDeletedMessageDetail = false" size="mini"> </el-button>
                    </div>
                </el-dialog>
            </el-dialog>
        </el-main>
    </el-container>
</template>

<!---->
<script>
export default {
    //
    data() {
        return {
            //
            mailBox: [{
                file: '',
                fileNum: 0,
                noReadFile: 0
            },
            {
                file: '',
                fileNum: 0,
                noReadFile: 0
            },
            {
                file: '',
                fileNum: 0,
                noReadFile:0
            },
            {
                file: '',
                fileNum: 0,
                noReadFile: 0
            }
            ],

            sendedInfo: [],
            draftInfo: [],
            deletedInfo: [],


 //

            messageDetail: [
                {
                    size:"",
                    messageTitle:"",
                    messageTypeName:"",
                    createUserName:"",
                }

            ],


//
            sendMessageDetail:[
                {
                    size:"",
                    messageId:"",
                    messageTypeName:"",
                    messageTitle:"",
                    beginTime:""
                }
            ],

            //
            draftMessageDetail:[
                {
                    size:"",
                     messageId:"",
                    messageTypeName:"",
                    messageTitle:"",
                    beginTime:""

                }
            ],

             deleteMessageDetail:[
                {
                    size:"",
                     messageId:"",
                    messageTypeName:"",
                    messageTitle:"",
                    beginTime:""

                }
            ],


            // 
            recipientsContent: [
                {
                    userId:"",
                    chineseName:"",
                    
                }
            ],


            messages:[],
            message1:[],
            sendDetail:[],
            draftDetail:[],
            deleteDetail:[],
            sender:'',
            receivedmessage:[],

            dialogInboxVisible: false,
            dialogSendedVisible: false,
            dialogDraftVisible: false,
            dialogDeletedVisible: false,

            dialogMessageDetail: false,
            dialogSendedMessageDetail: false,
            dialogDraftMessageDetail: false,
            dialogDeletedMessageDetail: false,

            newMessgae: true,
            multipleSelection: []
        }
    },

    //
    mounted() {
      this.getAllInboxInfo()
      this.getAllSendedInfo()
      this.getAllDraftInfo()
      this.getAllDeletedInfo()
    },

    //
    methods: {
        // ()
        ifColor(row){
            if(row.ifReadByUser === "1"){
                return "primary"
            }else{
                return "danger"
            }
        },

        //
        ifRead(row){
            if(row.ifReadByUser === "1"){
                return ""
            }else{
                return ""
            }
        },

        //
        getAllInboxInfo() {
             var messageDetail = {
                 "size":"",
                 "notReadSize":""
             }

             
            this.getRequest('/message/').then(resp => {
            console.log("receiveResp:",resp);
                this.messageDetail.size = resp.data.obj.size;
                this.messageDetail.notReadSize = resp.data.obj.notReadSize;
                this.messageDetail.ifReadByUser = resp.data.obj.ifReadByUser;
                this.messages = resp.data.obj.vos;
                
                console.log("===== ",this.messageDetail.size);
                console.log(":",this.messageDetail.notReadSize);
                console.log("ifReadByUser?:",this.messageDetail.ifReadByUser);
                console.log("",this.messages);
                this.mailBox[0].fileNum = this.messageDetail.size;
                this.mailBox[0].noReadFile = this.messageDetail.notReadSize;
                
            })
        },
        
        //
        getAllSendedInfo() {
            var sendMessageDetail = {
                 "size":"",
             }
            this.getRequest('/message/getPublishedMessages').then(resp => {
                console.log("receiveResp:",resp);
                this.sendMessageDetail.size = resp.data.obj.size;
                this.sendDetail = resp.data.obj.vos;
                console.log("",this.sendMessageDetail.size);
                console.log("",this.sendDetail);
                this.mailBox[2].fileNum = this.sendMessageDetail.size;
                
            })
        },

        //
        getAllDraftInfo(){
           var draftMessageDetail = {
                 "size":"",
             }
            this.getRequest('/message/getNotPublishedMessages').then(resp => {
                console.log("receiveResp:",resp);
                this.draftMessageDetail.size = resp.data.obj.size;
                this.draftMessageDetail.ifReadByUser = resp.data.obj.ifReadByUser;

                this.draftDetail = resp.data.obj.vos;
                
                console.log("=====",this.draftMessageDetail.size);
                console.log("",this.draftDetail);
                console.log("ifNotReadByUser?:",this.draftMessageDetail.ifReadByUser);
                this.mailBox[1].fileNum = this.draftMessageDetail.size;
                
            })
        },

        //
        getAllDeletedInfo(){
           var deleteMessageDetail = {
                 "size":"",
                 "notReadSize" : ""
             }
            this.getRequest('/message/getDeletedMessages').then(resp => {
                console.log(" receiveResp:",resp);
                this.deleteMessageDetail.size = resp.data.obj.size;
                this.deleteDetail = resp.data.obj.vos;
                this.deleteMessageDetail.notReadSize = resp.data.obj.notReadSize;
                console.log("=====",this.deleteMessageDetail.notReadSize);
                console.log("",this.deleteMessageDetail.size);
                console.log("",this.deleteDetail);
                this.mailBox[3].fileNum = this.deleteMessageDetail.size;
                this.mailBox[3].noReadFile = this.deleteMessageDetail.notReadSize;
                
            })
        },

        //
        showDetail(row) {
            if (row.file === '') {
            this.dialogInboxVisible = true
            console.log("  showDetail");
            
            this.getAllInboxInfo(row)

            } else if (row.file === '') {
            this.dialogSendedVisible = true
            console.log("  showDetail");
            this.getAllSendedInfo(row)

            } else if (row.file === '') {
            this.dialogDraftVisible = true
            console.log("  showDetail");
            this.getAllDraftInfo(row)

            } else if (row.file === '') {
            this.dialogDeletedVisible = true
            console.log("  showDetail");
            this.getAllDeletedInfo(row)
            }
        },
        
        //
        showMessageDetail(row) {
            var _this = this
            _this.dialogMessageDetail = true
    
            this.message1 = row;
        
            this.receivedmessage = row.recipients;

            this.sender = row.createUserName;
            console.log("=====recipients",this.receivedmessage)
            console.log("",this.sender)
            console.log(":",this.message1)

            var datas = {
                "currentUserId": this.$store.state.user.userId,
                "messageIds": [
                    row.messageId
                ]
            }
            this.postRequest('/message/read', datas).then(resp => {
                if (resp && resp.status === 200) {
                    this.$message({
                        type: 'success',
                        message: '!'
                    })
                }
                this.getAllInboxInfo()
            })
        },
        
        //
        showSendedMessageDetail(row) {
            var _this = this
            _this.dialogSendedMessageDetail = true
            
            _this.messageDetail = row
            for (let i = 0; i < _this.sendedInfo.length; i++) {
            if (_this.sendedInfo[i].id === row.id) {
                _this.sendedInfo[i].newMessage = ''
            }
            }
            console.log(_this.sendedInfo)
        },

        //
        showDraftMessageDetail(row){
            var _this = this
            _this.dialogDraftMessageDetail = true
            _this.messageDetail = row
            for (let i = 0; i < _this.draftInfo.length; i++) {
            if (_this.draftInfo[i].id === row.id) {
                _this.draftInfo[i].newMessage = ''
            }
            }
            console.log(_this.draftInfo)
        },

        //
        showDeletedMessageDetail(row){
            var _this = this
            _this.dialogDeletedMessageDetail = true
            _this.messageDetail = row
            for (let i = 0; i < _this.deletedInfo.length; i++) {
            if (_this.deletedInfo[i].id === row.id) {
                _this.deletedInfo[i].newMessage = ''
            }
            }
            console.log(_this.deletedInfo)

            var datas = {
                "currentUserId": this.$store.state.user.userId,
                "messageIds": [
                    row.messageId
                ]
            }
            this.postRequest('/message/read', datas).then(resp => {
                if (resp && resp.status === 200) {
                    this.$message({
                        type: 'success',
                        message: '!'
                    })
                }
                this.getAllInboxInfo()
            })
        },
        
        //
        handleSelectionChange(val) {
            this.multipleSelection = val
        },
       

//
        // deleteInboxMessage(row) {
        //     this.$confirm('[' + this.multipleSelection.length + '], ?', '', {
        //     // this.$confirm(', ?', '', {
        //     confirmButtonText: '',
        //     cancelButtonText: '',
        //     type: 'warning'
        //     }).then(() => {
        //     var ids = []
        //     ids.push(row.messageId)
        //     var datas ={
        //         "ids":ids
        //     }
        //     console.log("shima",datas);
        //     console.log("ids:",ids);
        //     this.postRequest('/message/deleteMessagesToCollection', datas).then(resp => {
        //         this.tableLoading = false
        //         console.log("PassId",datas);
        //         console.log("deleteMesagesToCollection");
        //         if (resp.status === 200) {
        //         this.$message({
        //             type: 'success',
        //             message: '!'
        //         })
        //         this.getAllInboxInfo()
        //         }
        //      }
        //     )
        //     }).catch(() => {})
        // },


          deleteInboxMessage(row) {
             this.$confirm('[' + this.multipleSelection.length + '], ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var ids = []
          for (var i = 0; i < this.multipleSelection.length; i++) {
                ids.push(this.multipleSelection[i].messageId)
          }
          var datas ={
            ids:ids
            }
         console.log("",datas);
          this.postRequest('/message/deleteMessagesToCollection', datas).then(resp => {
            this.tableLoading = false
            if (resp && resp.status === 200) {
              this.$message({
                type: 'success',
                message: '!'
              })
              this.getAllDeletedInfo()
            }
          })
        }).catch(() => {})
        },
        


        
        //
        // deleteSendedMessage(row) {
        //      this.$confirm('[' + this.multipleSelection.length + '], ?', '', {
        //     confirmButtonText: '',
        //     cancelButtonText: '',
        //     type: 'warning'
        //     }).then(() => {
        //     var ids = []
        //     ids.push(row.messageId)
        //     var datas ={
        //         "ids":ids
        //     }
        //     console.log("shima",datas);
        //     console.log("ids:",ids);
        //     this.postRequest('/message/deleteMessagesToCollection', datas).then(resp => {
        //         this.tableLoading = false
        //         console.log("PassId",datas);
        //         console.log("deleteMesagesToCollection");
        //         if (resp.status === 200) {
        //         this.$message({
        //             type: 'success',
        //             message: '!'
        //         })
        //         this.getAllSendedInfo()
        //         }
        //      }
        //     )
        //     }).catch(() => {})
        // },

deleteSendedMessage() {
            this.$confirm('[' + this.multipleSelection.length + '], ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var ids = []
          for (var i = 0; i < this.multipleSelection.length; i++) {
                ids.push(this.multipleSelection[i].messageId)
          }
          var datas ={
            ids:ids
            }
         console.log("",datas);
          this.postRequest('/message/deleteMessagesToCollection', datas).then(resp => {
            this.tableLoading = false
            if (resp && resp.status === 200) {
              this.$message({
                type: 'success',
                message: '!'
              })
              this.getAllDeletedInfo()
            }
          })
        }).catch(() => {})
        },




        //
        // deleteDraftMessage(row){
        //     this.$confirm('[' + this.multipleSelection.length + '], ?', '', {
        //     confirmButtonText: '',
        //     cancelButtonText: '',
        //     type: 'warning'
        //     }).then(() => {
        //     var ids = []
        //     ids.push(row.messageId)
        //     var datas ={
        //         "ids":ids
