<!---->
<template>
    <!---->
    <el-container>
        <!---->
        <el-header>
            <!---->
            <div>
                <el-header class="header">
                    
                    <hr width="300px" color="#20a0ff">
                </el-header>
            </div>
        </el-header>

        <!---->
        <el-main>
            <!---->
            <el-form :inline="true" :model="WriteTime" class="demo-form-inline" style="margin:20px auto">
                <!--<el-form-item label="">
                    <el-date-picker v-model="WriteTime.start" type="date" placeholder=""></el-date-picker>
                </el-form-item>
                <span>_ _ _ _ _</span>
                <el-form-item label="">
                    <el-date-picker v-model="WriteTime.end" type="date" placeholder=""></el-date-picker>
                </el-form-item>-->
                <el-form-item label="">
                    <el-date-picker
                        v-model="WriteTime.dateScope"
                        type="datetimerange"
                        align="right"
                        unlink-panels
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        value-format="yyyy-MM-dd HH:mm:ss"
                        :picker-options="pickerOptions">
                    </el-date-picker>
                </el-form-item>

                <el-form-item>
                    <el-radio-group v-model="WriteTime.limit">
                        <el-radio :label="1"></el-radio>
                        <el-radio :label="2"></el-radio>
                        <el-radio :label="3"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" size="mini" @click="searchMessage"><i class="el-icon-search"></i></el-button>
                </el-form-item>
            </el-form>

            <hr>

            <!---->
            <p>
                <el-button type="primary" @click="dialogMessageFormVisible = true" class="el-icon-circle-plus" size="middle"></el-button>
            </p>

            <!---->
            <el-table :data="message" border style="width: 95%;margin:0 auto" size="mini" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
                <el-table-column prop="messageTitle" label="" width="180px" align="center" fixed="left"></el-table-column>

                <el-table-column prop="messageTypeName" label="" width="180px" align="center"></el-table-column>

                <el-table-column prop="messageContent" label="" width="180px" align="center">
                    <template slot-scope="scope">
                        <span @click="showContentDetail(scope.row)">{{scope.row.messageContent}}</span>
                    </template>
                </el-table-column>

                <el-table-column prop="createUserName" label="" width="180px" align="center"></el-table-column>

                <el-table-column prop="recipients" label="" width="180px" align="center">
                    <template slot-scope="scope">
                        <!--<span @click="showToUserId(scope.row)">{{scope.row.chineseName}}</span>-->
                        <i class="el-icon-user" @click="showToUserId(scope.row)"></i>
                    </template>
                </el-table-column>

                <el-table-column prop="beginTime" label="" width="180px" align="center"></el-table-column>

                <el-table-column prop="endTime" label="" width="180px" align="center"></el-table-column>

                <el-table-column prop="createTime" label="" width="180px" align="center"></el-table-column>

                <el-table-column label="" align="center" fixed="right">
                    <template slot-scope="scope">
                        <i class="el-icon-edit" @click="editMessage(scope.row)"></i>
                    </template>
                </el-table-column>

                <el-table-column label="" align="center" fixed="right">
                    <template slot-scope="scope">
                        <i class="el-icon-close colorRed" @click="deleteMessage(scope.row)"></i>
                    </template>
                </el-table-column>

                <el-table-column label="" align="center" width="100px" fixed="right">
                    <template slot-scope="scope">
                        <el-button @click="pulish(scope.row)" :disabled="ifPublish(scope.row)" type="primary" size="mini"> </el-button>
                    </template>
                </el-table-column>

            </el-table>

            <!--  -->
            <el-dialog title="" :visible.sync="dialogMessageVisible">
                <el-form :model="basicContent" ref="basicContent">
                    <el-form-item>
                        <el-input type="textarea" v-model="basicContent.content" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer" align="center">
                    <el-button @click="dialogMessageVisible = false" type="danger"> </el-button>
                </div>
            </el-dialog>

            <!--  -->
            <el-dialog title="" :visible.sync="dialogToUserVisible">
                <el-form :model="basicContent" ref="basicContent">
                    <el-form-item>
                        <el-input type="textarea" v-model="basicContent.chineseName" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer" align="center">
                    <el-button @click="leave" type="danger"> </el-button>
                </div>
            </el-dialog>

            <!--/-->
            <el-dialog title="" :visible.sync="dialogMessageFormVisible">
                <el-form :model="messageInfoForm">
                    <!---->
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="messageInfoForm.title" autocomplete="off" placeholder="" style="width:100%"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!---->
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-select v-model="messageInfoForm.type" placeholder="" style="width:100%">
                                    <el-option label="" value=1></el-option>
                                    <el-option label="" value=2></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!--<el-row>
                        <el-col :span="12">
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-date-picker v-model="messageInfoForm.startTime" type="datetime" placeholder="" style="width:100%"></el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>-->
                    
                    
                    <!--<el-row>
                        <el-col :span="12">
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-date-picker v-model="messageInfoForm.endTime" type="datetime" placeholder="" style="width:100%"></el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>-->

                    <!---->
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="">
                                <el-date-picker
                                    v-model="messageInfoForm.dateScope"
                                    type="datetimerange"
                                    align="right"
                                    unlink-panels
                                    range-separator=""
                                    start-placeholder=""
                                    end-placeholder=""
                                    value-format="yyyy-MM-dd HH:mm:ss"
                                    :picker-options="pickerOptions">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    

                    <!---->
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" :label-width="formLabelWidth">
                                <!--messageInfoForm.toUserIdlabelchange-->
                                <el-radio-group v-model="messageInfoForm.toUserId" @change="toUserChange">
                                    <el-radio label="1" :disabled="toUserIdButton"></el-radio>
                                    <el-radio label="2" :disabled="toUserIdButton"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!---->
                    <!--  -->
                    <el-row v-if="showRange">
                        <fieldset>
                            <legend align="left"><i class="el-icon-search"></i></legend>
                            <el-form>
                                <!---->
                                <el-row>
                                    <el-col :span="3">
                                        <el-form-item>
                                            <el-checkbox v-model="checkedByBranch" @change="showScreenRange()"></el-checkbox>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="3">
                                        <el-form-item>
                                            <el-checkbox v-model="checkedByDepart" @change="showScreenRange()"></el-checkbox>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="3">
                                        <el-form-item>
                                            <el-checkbox v-model="checkedByUserId" @change="showScreenRange()"></el-checkbox>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="3">
                                        <el-form-item>
                                            <el-checkbox v-model="checkedByUserName" @change="showScreenRange()"></el-checkbox>
                                        </el-form-item>
                                    </el-col>
                                </el-row>

                                <!---->
                                <el-row>
                                    <el-col :span="6">
                                        <el-form-item label="" label-width="50px">
                                            <el-select v-model="branchName" placeholder="" :disabled="ifSelectBranch" @change="getBranch">
                                                <el-option 
                                                    v-for="item in branchs" 
                                                    :key="item.branchId" 
                                                    :label="item.branchName" 
                                                    :value="item.branchName">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="6">
                                        <el-form-item label="" label-width="50px">
                                            <el-select v-model="departName" placeholder="" :disabled="ifSelectDepart" @change="getDepart">
                                                <el-option 
                                                    v-for="item in branchSubDepart" 
                                                    :key="item.departId" 
                                                    :label="item.departName" 
                                                    :value="item.departName">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="6">
                                        <el-form-item label="" label-width="70px">
                                            <el-input v-model="selectUserId" placeholder="" :disabled="ifInputUserId"></el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="6">
                                        <el-form-item label="" label-width="50px">
                                            <el-input v-model="selectUserName" placeholder="" :disabled="ifInputUserName"></el-input>
                                        </el-form-item>
                                    </el-col>
                                </el-row>
                                
                                <!---->
                                <el-row>
                                    <el-button type="primary" @click="getUser"></el-button>
                                </el-row>
                            </el-form>
                        </fieldset>
                    </el-row>

                    <!---->
                    <el-row>
                        <fieldset v-if="showUser">
                            <legend align="left"><i class="el-icon-user"></i></legend>
                            <el-form>
                                <!---->
                                <el-row>
                                    <template>
                                        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
                                        <div style="margin: 15px 0;"></div>
                                        <el-checkbox-group v-model="checkedUsers" @change="handleCheckedUsersChange">
                                            <el-checkbox v-for="user in userOptions" :label="user" :key="user.userId">{{user.chineseName}}</el-checkbox>
                                        </el-checkbox-group>
                                    </template>
                                </el-row>
                            </el-form>
                        </fieldset>
                    </el-row>

                    <!---->
                    <el-row>
                        <el-col>
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input type="textarea" v-model="messageInfoForm.content" autocomplete="off" placeholder="" style="width:100%"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>

                <div slot="footer" class="dialog-footer" style="text-align:center">
                    <el-button type="primary" @click="addOrUpdateMessage"></el-button>
                    <el-button @click="cancel()"> </el-button>
                </div>
            </el-dialog>  
        </el-main>
    </el-container>
</template>

<!---->
<script>
export default {
    //
    data() {
        return {
            
            WriteTime: {
                dateScope: [],
                limit: ''
            },

            message: [],

            basicContent: {
                content: '',
                toUserId: [],
                chineseName: []
            },

            dialogMessageVisible: false,
            dialogToUserVisible: false,
            dialogMessageFormVisible: false,

            messageInfoForm: {
                messageId: null,
                title: '',
                type: '',
                //startTime: '',
                //endTime: '',
                dateScope: [],
                toUserIds: [],
                content: ''
            },

            toUserIdButton: false,
            showRange: false,//
            showUser: false,//

            checkedByBranch: '',
            checkedByDepart: '',
            checkedByUserId: '',
            checkedByUserName: '',

            selectBranchId: '',
            ifSelectBranch: '',

            selectDepartId: '',
            ifSelectDepart: '',

            selectUserId: '',
            ifInputUserId: '',

            selectUserName: '',
            ifInputUserName: '',

            pulishButton: false,

            branchs: [],
            branchSubDepart: [],

            branchName:'',
            departName:'',

            userDatas: [],
            userOptions: [],
            checkAll: false,
            checkedUsers: [],
            users: [],
            isIndeterminate: true,

        }
    },

    //
    created(){
        this.getBranch()
    },

    //
    mounted() {
      //this.loadMessages();
      this.getAllBranchsInfo();
      this.getAlldepartmentsInfo();
      this.getBranch();//
    },

    watch:{
        branchs:{
          handler(newValue, oldValue) {},
          deep:true
        },
        branchSubDepart:{
          handler(newValue, oldValue) {},
          deep:true
        }
    },

    //
    methods:{
        //"2019-09-19 12:57:41"
        getCurrentDate() {
            var now = new Date();
            var year = now.getFullYear(); //
            var month = now.getMonth();//
            var date = now.getDate();//
            //var day = now.getDay();//
            var hour = now.getHours();//
            var minu = now.getMinutes();//
            var sec = now.getSeconds();//
            month = month + 1;
            if (month < 10) month = "0" + month;
            if (date < 10) date = "0" + date;
            if (hour < 10) hour = "0" + hour;
            if (minu < 10) minu = "0" + minu;
            if (sec < 10) sec = "0" + sec;
            var time = "";
            //"2019-09-19 12:57:41"
            time = year + "-" + month + "-" + date+ " " + hour + ":" + minu + ":" + sec;
            return time;
        },

        //
        getBranch(){
            let tempValue = []
            var branchs = {"branchName":""}
            this.getRequest("/admin/attendsearch/branch").then(resp=> {
                //console.log("",resp);
                this.branchs= resp.data;
                //console.log("",this.message.branchName);
                this.getDepart();
            
            })
        },

        //
        getDepart(){
            let tempValue = []
            var branchSubDepart = {"departName":""}
            //console.log("",this.message.branchName);
            this.getRequest("/admin/attendsearch/"+ this.branchName).then(resp=> {
                //console.log("",resp);
                this.branchSubDepart = resp.data;
                //console.log("",this.departNames);
                //console.log("this.departNames",this.departNames);
            })
        },

        WriteTime(){

        },

        basicContent(){

        },

        messageInfoForm(){

        },

        //
        searchMessage(){
            var _this = this;
            var datas = {
                "endTime": _this.WriteTime.dateScope[1],
                "limit": _this.WriteTime.limit,
                "messageTypeId": 0,
                "query": null,
                //"size": 0,
                //"start": 0,
                "startTime": _this.WriteTime.dateScope[0]
            }
            this.postRequest('/message/list', datas).then(resp => {
                if (resp && resp.status === 200) {
                    console.log("test",resp)
                    _this.message = resp.data.obj.vos
                }
            })
        },

        //
        ifPublish(row){
            console.log("=================================")
            console.log(row)
            console.log("=================================")
            if(row.isPublished === '0'){//
                return this.pulishButton = false;
            }else{//
                return this.pulishButton = true;
            }
        },

        //
        showContentDetail(row){
            this.dialogMessageVisible = true
            this.basicContent.content = row.messageContent
        },

        //
        showToUserId(row){
            this.dialogToUserVisible = true
            this.basicContent.toUserId = row.recipients
            console.log("testArray",this.basicContent.toUserId)
            //this.basicContent.chineseName = row.recipients[0].chineseName
            for(var i of row.recipients){
                this.basicContent.chineseName.push(i.chineseName)
            }
        },

        //
        leave(){
            this.dialogToUserVisible = false;
            for(;this.basicContent.chineseName.length>0;){
                this.basicContent.chineseName.pop()
            }
        },

        ///messageId
        addOrUpdateMessage(){
            this.dialogMessageFormVisible = true
            var _this = this
            if(_this.messageInfoForm.type === ""){
                _this.messageInfoForm.type = 1
            }else{
                _this.messageInfoForm.type = 2
            }
            var datas = {
                "beginTime": _this.messageInfoForm.dateScope[0],
                "createTime": _this.getCurrentDate(),
                "createUserId": _this.$store.state.user.userId,
                "endTime": _this.messageInfoForm.dateScope[1],
                "messageContent": _this.messageInfoForm.content,
                "messageId": _this.messageInfoForm.messageId,
                "messageTitle": _this.messageInfoForm.title,
                "messageTypeId": _this.messageInfoForm.type,
                "recipientIds": _this.messageInfoForm.toUserIds
            }
            console.log("datas",datas)

            if(_this.messageInfoForm.messageId === null){//id
                
                this.postRequest('/message/add', datas).then(resp => {
                    if (resp && resp.status === 200) {
                        _this.emptyMessageInfoForm()//
                        _this.searchMessage()//
                    }
                })
            }else{//id
                this.postRequest('/message/update', datas).then(resp =>{
                    if (resp && resp.status === 200) {
                        _this.emptyMessageInfoForm()//
                        _this.searchMessage()//
                    }
                })
            }
            
        },

        ///
        emptyMessageInfoForm(){
            this.dialogMessageFormVisible = false
            this.messageInfoForm.messageId = null
            this.messageInfoForm.title = ''
            this.messageInfoForm.type = ''
            for(;this.messageInfoForm.dateScope.length>0;){
                this.messageInfoForm.dateScope.pop()
            }
            //this.messageInfoForm.toUserIds = row.
            for(;this.messageInfoForm.toUserIds.length>0;){
                this.messageInfoForm.toUserIds.pop()
            }
            this.messageInfoForm.content = ''

            this.showRange = false
            this.showUser = false

            this.branchName = ''
            this.departName = ''
            this.selectUserId = ''
            this.selectUserName = ''

            for(;this.checkedUsers.length>0;){
                this.checkedUsers.pop()
            }

            for(;this.userOptions.length>0;){
                this.userOptions.pop()
            }
                
            
        },

        //
        editMessage(row){
            this.dialogMessageFormVisible = true
            this.showRange = true
            this.showUser = true
            this.messageInfoForm.messageId = row.messageId
            this.messageInfoForm.title = row.messageTitle
            this.messageInfoForm.type = row.messageTypeName
            this.messageInfoForm.dateScope[0] = row.beginTime
            this.messageInfoForm.dateScope[1] = row.endTime
            //this.messageInfoForm.toUserIds = row.recipients.userId
            for(var i of row.recipients){
                this.messageInfoForm.toUserIds.push(i.userId)
                this.checkedUsers.push(i.userId)
                this.userOptions.push(i)
            }
            this.messageInfoForm.content = row.messageContent
        },

        //
        deleteMessage(row){
            var _this = this;
            var ids = []
            ids.push(row.messageId)
            var datas = {
                "ids": ids
            }
            this.postRequest('/message/deleteMessages', datas).then(resp => {
                if (resp && resp.status === 200) {
                   _this.searchMessage()//
                }
            })
        },

        //
        pulish(row){ 
            var _this = this
            var messageId = row.messageId
            this.getRequest('/message/publish/'+messageId).then(resp => {
                if (resp && resp.data.status === 200) {
                    _this.pulishButton = true;//
                    _this.searchMessage();//
                }
            })
        },

        //
        toUserChange(value){
            //
            for(;this.checkedUsers.length>0;){
                this.checkedUsers.pop()
            }
            for(;this.userOptions.length>0;){
                this.userOptions.pop()
            }
            for(;this.messageInfoForm.toUserIds.length>0;){
                this.messageInfoForm.toUserIds.pop()
            }

            if(value === '1'){
                this.showRange = false;
                this.showUser = false;
            }
            if(value === '2'){
                //console.log("testRadio")
                this.showRange = true;
            }
        },
        
        //
        showScreenRange(){
            if (this.checkedByBranch) {//
                this.ifSelectBranch = false
                this.ifSelectDepart = true
                this.ifInputUserId = true
                this.ifInputUserName = true
                this.checkedByDepart = false
                this.checkedByUserId = false
                this.checkedByUserName = false
            } else if (this.checkedByDepart) {//
                this.ifSelectBranch = false
                this.ifSelectDepart = false
                this.ifInputUserId = true
                this.ifInputUserName = true
                this.checkedByBranch = false
                this.checkedByUserId = false
                this.checkedByUserName = false
            } else if (this.checkedByUserId) {//
                this.ifSelectBranch = true
                this.ifSelectDepart = true
                this.ifInputUserId = false
                this.ifInputUserName = true
                this.checkedByBranch = false
                this.checkedByDepart = false
                this.checkedByUserName = false
            } else if (this.checkedByUserName) {//
                this.ifSelectBranch = true
                this.ifSelectDepart = true
                this.ifInputUserId = true
                this.ifInputUserName = false
                this.checkedByBranch = false
                this.checkedByDepart = false
                this.checkedByUserId = false
            } else if(this.checkedByDay)
            {
                this.ifSelectWeek = true
                this.ifSelectMonth = true
                this.checkedByWeek = false
                this.checkedByMonth = false
            }
            else if(this.checkedByWeek)
            {
                this.ifSelectDay = true
                this.ifSelectMonth = true
                this.ifSelectDay = false
                this.checkedByMonth = false
            }else if(this.checkedByMonth)
            {
                this.ifSelectWeek = true
                this.ifSelectDay = true
                this.ifSelectDay = false
                this.checkedByWeek = false
            }
            else {
                this.ifSelectBranch = true
                this.ifSelectDepart = true
                this.ifInputUserId = true
                this.ifInputUserName = true
                this.checkedByBranch = false
                this.checkedByDepart = false
                this.checkedByUserId = false
                this.checkedByUserName = false
            }
        },

        //
        cancel(){
            this.emptyMessageInfoForm()
        },

        //
        getUser(){
            //
            for(;this.checkedUsers.length>0;){
                this.checkedUsers.pop()
            }

            for(;this.userOptions.length>0;){
                this.userOptions.pop()
            }

            for(;this.messageInfoForm.toUserIds.length>0;){
                this.messageInfoForm.toUserIds.pop()
            }

            this.showUser = true
            var _this = this
            var datas = {
                "branchName": _this.branchName,
                "chineseName": _this.selectUserName,
                "departName": _this.departName,
                "userId": _this.selectUserId
            }
            this.postRequest('/message/selectUsers', datas).then(resp => {
                if (resp && resp.status === 200) {
                   //_this.searchMessage()//
                   console.log("testSelectUsers",resp)
                   _this.userOptions = resp.data.obj
                   for(var user of _this.userOptions){

                       _this.userDatas.push(user.chineseName)
                   }
                   _this.users = _this.userDatas
                   console.log("testUsers",_this.users)
                }
            })
        },

        //
        handleCheckAllChange(val) {
            this.checkedUsers = val ? userOptions : [];
            this.isIndeterminate = false;
            for(var check of this.checkedUsers){
                this.messageInfoForm.toUserIds.push(check.userId)
            }
        },

        //
        handleCheckedUsersChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.users.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.users.length;
            for(var check of this.checkedUsers){
                this.messageInfoForm.toUserIds.push(check.userId)
            }
            console.log("toUserIds",this.messageInfoForm.toUserIds)
        }
    }
}
</script>

